
import XProgress from 'vux/src/components/x-progress/index.vue'
import Box from 'vux/src/components/box/index.vue'

import Vue from "vue";
import Vuetify from "vuetify";

Vue.use(Vuetify);
import "vuetify/dist/vuetify.min.css";
var list = [];
export default {
components: {
XProgress,
Box
},
data() {
return {
uploads: list
};
},
mounted() {
this.search();
},
methods: {
search() {
this.$post(URL.get_progress).then(data => {
this.uploads = [];
for (var i = 0; i < data.object.objects.length; i++) {
this.uploads.push(data.object.objects[i]);
}
});
}
}
};



