
import XProgress from 'vux/src/components/x-progress/index.vue'
import Box from 'vux/src/components/box/index.vue'

import Vue from 'vue'
import Vuetify from 'vuetify'
var uploadObj=[];
var headList=[];
var tailList=[];
Vue.use(Vuetify)
import 'vuetify/dist/vuetify.min.css'

export default {
components: {
XProgress,
Box
},
data () {
return {
uploadObj:uploadObj,
headList:headList,
tailList:tailList,
photourl: ""
}
}, mounted(){

this.search();

},methods:{
click(url) {
this.photourl = url;
this.$refs.imgDialog.changeShow();
},search(){
var params = new URLSearchParams();
params.append('id',this.$route.query.id);
this.$post(URL.get_upload_by_id,params).then(data => {
this.uploadObj =[];
this.headList = [];
this.tailList = [];
for (var i = 0; i < data.object.tailList.length; i++) {
this.tailList.push(data.object.tailList[i]);
}
for(var j = 0; j < data.object.headList.length; j++){
this.headList.push(data.object.headList[j])
}
for(var a = 0; a < data.object.object.length; a++){
this.uploadObj.push(data.object.object[a])
}
});
},back(){
this.$router.push("/uploadHistory");
}
}
}





