export default {
	 
	   data(){
			return {
				list:[],
			}
	   },
	   mounted(){
		this.search();
	   },
	   methods:{
			 search(){
					this.$post(URL.get_search_history)
					.then(data=>{
						this.list=[];
						this.list=data.object.object;
				})
			 },searchById(id){
				 		 this.$router.push({
							path: "/detail",
							query: { id:id}
						});
			 }
				
	  }
}



