
import XDialog from 'vux/src/components/x-dialog/index.vue'

import XInput from 'vux/src/components/x-input/index.vue'

export default {
components: {
XDialog
},
data() {
return {
dealerNo: "",
dealerName: "",
showDialog: false
};
},
methods: {
changeShow() {
this.showDialog = true;
},
changeHide() {
this.showDialog = false;
},
checkDealer() {
if ("" == this.dealerNo) {
this.$alert.alert("");
return;
}
var params = new URLSearchParams();
params.append("dealerNo", this.dealerNo);

this.$post(URL.check_dealer, params).then(data => {
if (data.object.object == null) {
this.dealerName = "";
} else {
this.dealerName = data.object.object.name;
}
});
},
bundDealer() {
var params = new URLSearchParams();
params.append("dealerNo", this.dealerNo);
this.$post(URL.bund_dealer, params).then(data => {
this.$alert.alert(data.msg);
if (data.code == 0) {
this.showDialog = false;
}
});
}
}
};



