
var isVerificationCodedown = true;
const TIME_COUNT = 60;
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XHeader
},
data(){
return {
userPhone:'',
authCode:'',
background:{},
color:{},
show: true,
count: '',
timer: null,
}
},
mounted(){
},
methods:{
fn(){
window.location.href=URL.go_index;
},
loninButtonClick(){
if(this.userPhone==''){
this.$alert.alert("")
return;
}
var myreg =/^[1][3,4,5,7,8,9][0-9]{9}$/;
if(!myreg.test(this.userPhone)){
this.$alert.alert("");
return false;
}
if(this.authCode==''){
this.$alert.alert("")
return;
}
var params = new URLSearchParams();
params.append('phoneNumber',this.userPhone);
params.append('authCode',this.authCode);
this.$post(URL.login,params)
.then(data=>{
if(data.code=="0"){
this.$alert.alert(data.msg);
}
if(data.code=="1"){
this.$alert.alert(data.msg,this.fn)
}
if(data.code=="2"){
this.$alert.alert("");
}
})
},getAuthCode(){
var myreg =/^[1][3,4,5,7,8,9][0-9]{9}$/;
if(this.userPhone==null||this.userPhone==''){
this.$alert.alert("");
return false;
}
if(!myreg.test(this.userPhone)){
this.$alert.alert("");
return false;
}
if(!isVerificationCodedown){
return;
}
this.twsAuthCodeSend(this.userPhone);
this.getCode();
},twsAuthCodeSend(phoneNumber) {
var params = new URLSearchParams();
params.append('phoneNumber',this.userPhone);
this.$post(URL.get_auth_code,params)
.then(data=>{
})
},getCode(){
if (!this.timer) {
this.count = TIME_COUNT;
this.show = false;
this.timer = setInterval(() => {
if (this.count > 0 && this.count <= TIME_COUNT) {
this.count--;
} else {
this.show = true;
clearInterval(this.timer);
this.timer = null;
}
}, 1000)
}
}

}
}



