
import Vue from "vue";
import FastClick from "fastclick";
import Header from "./comment/header.vue";
import VueRouter from "vue-router";
import App from "../../../src/App.vue";
import StoreInformation from "./page/store_information.vue";
import Me from "./page/me.vue";
import Index from "./page/index.vue";
import UploadHistory from "./page/upload_history.vue";
import Progress from "./page/progress.vue";
import Detail from "./page/detail.vue";
import Login from "./page/login.vue";
import imgDialog from "./comment/imgDialog.vue";
import Dialog from "./comment/dialog.vue";
import WX from "weixin-js-sdk";
Vue.prototype.$wx = WX;

import XImg from 'vux/src/components/x-img/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import WechatPlugin from 'vux/src/plugins/wechat/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'
import VuxComponentListData from 'vux/src/datas/vux_component_list.json'


Vue.component("buttonTab", ButtonTab);
Vue.component("buttonTabItem", ButtonTabItem);
Vue.component("xInput", XInput);
Vue.component("xTextarea", XTextarea);
Vue.component("popupPicker", PopupPicker);
Vue.component("group", Group);
Vue.component("check-icon", CheckIcon);
Vue.component("box", Box);
Vue.component("cell", Cell);
Vue.component("xButton", XButton);
Vue.component("x-table", XTable);
Vue.component("group-title", GroupTitle);
Vue.component("divider", Divider);
Vue.component("wx", WX);
Vue.component("v-dialog", Dialog);
Vue.component("x-imgDialog", imgDialog);
Vue.component("x-img", XImg);
Vue.component("checklist", Checklist);
Vue.component("x-address", XAddress);
Vue.component("confirm", Confirm);
Vue.component("v-header", Header);

import Axios from "axios";

Axios.defaults.withCredentials = true;
Vue.prototype.$http = Axios;
Axios.defaults.headers.post["Content-Type"] = "application/json";

Vue.use(VueRouter);
Vue.use(WechatPlugin);
Vue.use(LoadingPlugin);














Vue.prototype.$alert = AlertModule;

Vue.prototype.$alert.alert = function(msg, fn) {
AlertModule.show({
title: "",
content: msg,
onHide() {
if (fn != null) {
fn();
}
}
});
};
function go_login(){
window.location.href="https://www.peyesight.cn/star-server/resources/store_picture_upload.html#/login";

}

Vue.prototype.$post = function(url, data = {}) {
return new Promise((resolve, reject) => {
Axios.post(url, data).then(
response => {

if (response.data.success) {
resolve(response.data);
}

if(response.data.needLogin){













this.$alert.alert("",go_login())


}

if(!response.data.success) {
if(!response.data.msg){
AlertModule.show({
title: "",
content: response.data.msg
});
}
}
},
err => {

AlertModule.show({
title: "",
content: ""
});
}
);
});
};


const routes = [
{ path: "/storeInformation", component: StoreInformation },
{ path: "/me", component: Me },
{ path: "/uploadHistory", component: UploadHistory },
{ path: "/index", component: Index },
{ path: "/progress", component: Progress },
{ path: "/detail", component: Detail },
{ path: "/dialog", component: Dialog },
{ path: "/login", component: Login }
];

const router = new VueRouter({
routes
});

FastClick.attach(document.body);

Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount("#app-box");




