

import value2name from 'vux/src/filters/value2name.js'
import Search from 'vux/src/components/search/index.vue'

var uploadObj={storeNo:'',storeName:'',province:'',city:'',county:'',address:'',dealerId:'',type:'',headList:'',tailList:''};
var storeObj=[]
var curProvinceCode="";
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import { Input,Autocomplete,Col,Select,Option, } from 'element-ui';
import Vue from 'vue';
Vue.use(Input);
Vue.use(Autocomplete);
Vue.use(ElementUI);


export default {
directives: {
focus:function (el) {
el.focus();
}
},
components: {
Search,
Input,
Autocomplete,
Col,
Select,Option,
},
data(){
return {
dealerList:[],
dealerId:[],
storeList:[],
province:[],
city:[],
county:[],
storeName:'',
uploadObj:uploadObj,
storeObj:storeObj,
value: [],
showAddress: false,
demo1:false,
type:'2',
isshowSearch:false,
isDisable:false
}
},
mounted(){
this.searchDealer(2);
this.searchAllStore();
this.searchprovince();
},
methods:{
doShowAddress () {
this.showAddress = true
setTimeout(() => {
this.showAddress = false
}, 2000)
},
getName (value) {
return value2name(value, ChinaAddressV4Data)
},saveUplaod(){
var flag=true;
this.uploadObj.dealerId=this.dealerId[0];
if(this.demo1){
this.uploadObj.type='1'
}
if(!this.demo1){
this.uploadObj.type='2'
}
if(this.uploadObj.storeName==''){
this.$alert.alert("")
return;
}
if(!this.uploadObj.city){
this.$alert.alert("")
return;
}
if(!this.uploadObj.county){
this.$alert.alert("")
return;
}
if(!this.uploadObj.province){
this.$alert.alert("")
return;
}
if(this.uploadObj.address==''){
this.$alert.alert("")
return;
}
if(!this.uploadObj.dealerId){
this.$alert.alert("")
return;
}
var strTail = JSON.stringify(this.$route.query.jsonTailStr);
var strHead = JSON.stringify(this.$route.query.jsonHeadStr);
this.uploadObj.headList=strHead;
this.uploadObj.tailList=strTail;
this.isDisable = true
this.$post(URL.save_upload,this.uploadObj)
.then(data=>{
this.isDisable = false;
if(data.code==0){
this.$alert.alert("",this.fn);
}
if(data.code==1){
this.$alert.alert(data.msg);
}
})
},fn(){
sessionStorage.clear();
this.$router.push("/uploadHistory");
},searchDealer(type){
this.$post(URL.get_search_sale_dealer)
.then(data=>{
this.dealerList=[];
this.dealerList[0]=data.object.objects;
})
},searchAllStore(){
this.$post(URL.get_all_store)
.then(data=>{
this.storeObj=[];
this.storeList=data.object.objects;
for(var a=0;a<data.object.objects.length;a++){
var title=data.object.objects[a].name;
var key=data.object.objects[a].id;
var arrayResult=	{
'value':title,
'address':key
}
this.storeObj.push(arrayResult);
}
})
}, querySearch(queryString, cb) {
var restaurants = this.storeObj;
var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;

cb(results);
}, createFilter(queryString) {
return (restaurant) => {
return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
};
},onFocus(){
this.isshowSearch=true;
},handleSelect(item){
this.uploadObj.storeNo="";
this.uploadObj.storeName="";
this.uploadObj.address="";
this.uploadObj.province="";
this.uploadObj.city="";
this.uploadObj.county="";
this.uploadObj.storeName=this.storeName;
for(var a=0;a<this.storeList.length;a++){
if(item.address==this.storeList[a].id){
this.uploadObj.storeNo=this.storeList[a].storeno;
this.uploadObj.address=this.storeList[a].address;
this.uploadObj.province=this.storeList[a].province;
this.uploadObj.city=this.storeList[a].city;
this.uploadObj.county=this.storeList[a].county;
return;
}
}
this.isshowSearch=false;
},closeStoreList(){
this.uploadObj.storeName=this.storeName;
this.isshowSearch=false;
},searchprovince(){
if(typeof ChineseDistricts != "undefined"  ){

var provicesAllArray=ChineseDistricts["86"];
for(var item in provicesAllArray){
var provicesArray=provicesAllArray[item];
for(var i=0;i<provicesArray.length;i++){
var address=provicesArray[i].address;
this.province.push(address)
}
}
}
},proviceChange(){
this.getCity(this.uploadObj.province);
},cityChange(){
this.getdistrict(this.uploadObj.city);
},
getCity(obj){
var code= this.getProviceCode(obj);
var cityArray=ChineseDistricts[code];
var address;
var cityCount=0;
for(var item in cityArray){
cityCount=cityCount+1;
address=cityArray[item];
this.city.push(address);
}
},getdistrict(obj){
var code= this.getCityCode(obj);
var districtArray=ChineseDistricts[code];
var address;
for(var item in districtArray){
address=districtArray[item];
this.county.push(address);
}
},
getProviceCode(addressParam){
var provicesAllArray=ChineseDistricts["86"];
for(var item in provicesAllArray){
var provicesArray=provicesAllArray[item];
for(var i=0;i<provicesArray.length;i++){
var address=provicesArray[i].address;
var code=provicesArray[i].code;
if(address==addressParam){
curProvinceCode=code;
return code;
}
}
}
},getCityCode(addressParam){
var cityArray=ChineseDistricts[curProvinceCode];
for(var item in cityArray){
var address=cityArray[item];
var code=item;
if(address==addressParam){
return code;
}
}
}







}

}



